.footer{padding:70px 58px 72px}.footer .content{padding:56px 62px 26px 46px;background-color:#dcdde1;min-height:288px;border-radius:18px;-webkit-border-radius:18px;-moz-border-radius:18px;-ms-border-radius:18px;-o-border-radius:18px}.footer .content .top{padding-left:5%;display:flex;justify-content:space-between}.footer .content .top .hearten{padding-left:40px;letter-spacing:2px;text-align:left;font-size:18px;font-weight:600;line-height:1.5;font-family:Heavy,Helvetica,Arial,sans-serif}.footer .content .top .hearten p{text-indent:2em}.footer .content .left{height:100%;padding-left:10%}.footer .content .left .lTop{width:120px}.footer .content .left .lTop .mask{pointer-events:none}.footer .content .left .lCenter{min-height:100px}.footer .content .left .lCenter .text{display:inline-block;line-height:40px;vertical-align:top;color:#252525;text-shadow:0 1.5px 4px #000}.footer .content .left .lCenter .img{width:24px;height:24px;padding:8px}.footer .content .left .copyright{color:rgba(37,37,37,.5019607843)}.footer .content .right{height:100%;padding-top:10px;box-sizing:border-box}.footer .content .right .text{display:inline-block;line-height:40px;vertical-align:top;color:#252525;text-shadow:0 1.5px 4px #000}.footer .content .right .link{padding-bottom:20px}.footer .content .right .link .links{padding-left:70px;box-sizing:border-box}.footer .content .right .link .links a{color:#222f3e;font:400 14px/24px "微软雅黑";cursor:pointer;line-height:30px}.footer .content .right .contact{padding-bottom:20px}.footer .content .right .contact .contactItem{display:inline-block}.footer .content .right .contact .contactItem img{cursor:pointer;width:24px;height:24px;padding:8px}.footer .content .right .contact .contactItem img:hover{transform:scale(1.5);-webkit-transform:scale(1.5);-moz-transform:scale(1.5);-ms-transform:scale(1.5);-o-transform:scale(1.5)}.footer .content .right .copyright{padding-bottom:20px;color:rgba(37,37,37,.5019607843);display:none}@media(max-width: 992px){.footer{padding:3